  margin-bottom: 15px;
}

.accordion .card-header {
  padding: 0px;
  background-color: transparent;
  border: none;
}

.accordion .card-header .btn,
.accordion .card-header a {
  display: block;
  height: auto;
  position: relative;
  line-height: 1.5;
  padding: 25px;
  width: 100%;
  text-align: left;
  color: #1b223c;
  border-radius: calc(.25rem - 1px) calc(.25rem - 1px) 0 0;
  white-space: unset;
  -webkit-transition: all 0.3s 0.2s ease-in;
  -moz-transition: all 0.3s 0.2s ease-in;
  -ms-transition: all 0.3s 0.2s ease-in;
  -o-transition: all 0.3s 0.2s ease-in;
  transition: all 0.3s 0.2s ease-in;
}

.accordion .card-header .btn:after,
.accordion .card-header a:after {
  position: absolute;
  color: #8a909d;
  font-size: 15px;
  right: 25px;
  top: 50%;
  -moz-transform: translateY(-50%);
  -o-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}

.accordion .card-header .btn[aria-expanded="false"]:after,
.accordion .card-header a[aria-expanded="false"]:after {
  content: "\f067";
  font-family: 'FontAwesome';
  opacity: 0;
}

.accordion .card-header .btn[aria-expanded="true"],
.accordion .card-header a[aria-expanded="true"] {
  box-shadow: 0px 0px 19px rgba(229, 230, 235, 0.6) !important;
  color: #4c84ff;
  border-bottom: 1px solid #e5e9f2;
}

