    padding-left: 50px;
    padding-right: 50px;
  }
}

.nav-tabs.nav-style-border .nav-item {
  margin-right: 15px;
}

@media (min-width: 768px) {
  .nav-tabs.nav-style-border .nav-item {
    margin-right: 35px;
  }
}

@media (min-width: 1200px) {
  .nav-tabs.nav-style-border .nav-item {
    margin-right: 50px;
    margin-bottom: 0px;
  }
}

@media (min-width: 1500px) {
  .nav-tabs.nav-style-border .nav-item {
    margin-right: 80px;
  }
}

.nav-tabs.nav-style-border .nav-item .nav-link {
  padding: 0 0 20px 0;
  color: #8a909d;
  position: relative;
}

.nav-tabs.nav-style-border .nav-item .nav-link .type-name {
  display: block;
}

.nav-tabs.nav-style-border .nav-item .nav-link:after {
  content: '';
  position: absolute;
  bottom: 14px;
  left: 0;
  height: 2px;
  background: #4c84ff;
  width: 100%;
  opacity: 0;
  visibility: hidden;
  -webkit-transition: all 0.3s ease-in;
  -moz-transition: all 0.3s ease-in;
  -ms-transition: all 0.3s ease-in;
  -o-transition: all 0.3s ease-in;
  transition: all 0.3s ease-in;
}

@media (min-width: 480px) {
  .nav-tabs.nav-style-border .nav-item .nav-link:after {
    bottom: -1px;
  }
}

.nav-tabs.nav-style-border .nav-item .nav-link.active:after {
  opacity: 1;
  visibility: visible;
}

.nav-tabs.nav-style-border .nav-item .nav-link:hover, .nav-tabs.nav-style-border .nav-item .nav-link.active {
  border-color: transparent;
  background-color: transparent;
}

@media (min-width: 768px) {
  .nav-tabs.nav-style-border .nav-item:first-child {
    margin-bottom: 15px;
  }
}

@media (min-width: 992px) {
  .nav-tabs.nav-style-border .nav-item:first-child {
    margin-bottom: 0;
  }
}

.nav-tabs.nav-style-border .nav-item:last-child {
  margin-bottom: 0;
}

.nav-tabs.nav-stacked {
  margin-right: 0;
  border-bottom: 0;
  border-right: none;
}

@media (min-width: 768px) {
  .nav-tabs.nav-stacked {
    border-right: 1px solid #e5e9f2;
    padding-right: 0;
  }
}

.nav-tabs.nav-stacked .nav-item {
  margin: 0 !important;
  float: none !important;
  display: block !important;
}

.nav-tabs.nav-stacked .nav-item .nav-link {
  padding: 12px 15px;
  margin-right: 0px;
  border-right: 0px;
  border-color: #e5e9f2;
  border: 0px;
  margin-right: -1px;
}

.nav-tabs.nav-stacked .nav-item .nav-link i {
  padding-right: 10px;
}

.nav-tabs.nav-stacked .nav-item .nav-link.active {
  border: 1px solid #e5e9f2;
}

@media (min-width: 768px) {
  .nav-tabs.nav-stacked .nav-item .nav-link.active {
    border-right: 0px;
    border-radius: 0px;
    border-top-left-radius: 6px;
    border-bottom-left-radius: 6px;
  }
  .nav-tabs.nav-stacked .nav-item .nav-link.active:before {
    top: 0;
    width: 2px;
    height: 100%;
    bottom: 0px;
  }
}

/* Toto List */
.todo-single-item {
  margin-bottom: 35px;
  position: relative;
}

.todo-single-item > span {
  margin-left: 25px;
  line-height: 1.2;
  position: relative;
  display: inline-block;
  color: #000000;
}

.todo-single-item > span.badge {
  color: #ffffff;
  -webkit-transition: all 0.3s ease-in;
  -moz-transition: all 0.3s ease-in;
  -ms-transition: all 0.3s ease-in;
  -o-transition: all 0.3s ease-in;
  transition: all 0.3s ease-in;
  display: inline-table;
}

.todo-single-item i.fa {
  width: 18px;
  height: 18px;
  border: 1px solid #d4d5db;
  border-radius: 50%;
  margin-right: 6px;
  position: relative;
  overflow: hidden;
  text-align: center;
  line-height: 16px;
  position: absolute;
  left: 0;
  -webkit-transition: all 0.3s ease-in;
  -moz-transition: all 0.3s ease-in;
  -ms-transition: all 0.3s ease-in;
  -o-transition: all 0.3s ease-in;
  transition: all 0.3s ease-in;
}

.todo-single-item i.fa:hover {
  border-color: rgba(76, 132, 255, 0.5);
  background-color: rgba(76, 132, 255, 0.2);
  cursor: pointer;
}

.todo-input {
  display: none;
}

.todo-input input {
  margin-top: -5px;
  border: none;
  font-size: 15px;
  padding: 5px 10px;
}

.todo-input input:active, .todo-input input:focus {
  box-shadow: 0px 2px 8px rgba(27, 34, 60, 0.2);
}

.todo-single-item.finished > span {
  color: #8a909d;
  text-decoration: line-through;
}

.todo-single-item.finished > span.badge {
  text-decoration: none;
  background-color: #cfd0d8;
  color: #8a909d;
}

.todo-single-item.finished i.fa {
  background-color: #d4d5db;
  border-color: #d4d5db;
}

.todo-single-item.finished i.fa:after {
  content: "\f00c";
  font-size: 12px;
  color: #8a909d;
}

